import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";
import { Link } from "react-router-dom";

import notification from "../../../../components/notification";
import { Empty, Input, Tooltip } from "antd";

import Popconfirms from "../../../../components/feedback/popconfirm";
// import Box from '../../components/utility/box';
// import HelperText from "../../../../components/utility/helper-text";
import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Scrollbars from "../../../../components/utility/customScrollBar";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/companyMaster/locationType/actions";
import TableWrapper from "../../../Tables/antTables/antTable.style";
import CardWrapper, { Box, StatusTag } from "../../../App/app.style";
import getLanguage from "../../../App/MyLanguange";
// const Panel = Collapse.Panel;
// const Option = Select.Option;
const Search = Input.Search;

const redirectTo = "/dashboard/setup/menus";

class LocationType extends Component {
  state = {
    filter: {
      pagination: null
    },
    search: null
  };

  // Get All data diawal
  componentDidMount() {
    const { getLocationTypes } = this.props;
    getLocationTypes();
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.locationType.error) {
      notification("error", this.props.locationType.error);
      this.props.clearError();
    }

    // Display success notif
    if (this.props.locationType.success) {
      notification("success", this.props.locationType.success);
      this.props.clearSuccess();
    }
  }

  // Handle search
  handleSearch = value => {
    console.log(value);
    const { getLocationTypes } = this.props;
    this.setState({
      filter: {
        id_account: [],
        pagination: null
      }
    });
    getLocationTypes(1, value);
  };

  //handle Search Value
  handleSearchValue = e => {
    const value = e.target.value;

    this.setState({ search: value });
  };

  // Handle Reset search
  handleResetSearch = () => {
    const { getLocationTypes } = this.props;
    this.setState({
      filter: {
        pagination: null
      }
    });
    this.setState({ search: null });
    getLocationTypes();
  };

  // Handle filter change
  handleFilterChange = (name, value) => {
    this.setState({
      filter: {
        ...this.state.filter,
        [name]: value
      }
    });
  };

  // Handle filter
  handleFilter = () => {
    const { getLocationTypes } = this.props;
    //const { current_page } = this.props.locationType;
    const { filter } = this.state;
    let string = "";

    if (filter.id_account) {
      string += "id_account=" + filter.id_account;
    }
    if (filter.pagination) {
      string
        ? (string += "&pagination=" + filter.pagination)
        : (string += "pagination=" + filter.pagination);
    }

    this.setState({ search: null });
    getLocationTypes(1, null, string);
  };

  // Handle Reset Filter
  handleResetFilter = () => {
    const { getLocationTypes } = this.props;
    this.setState({
      filter: {
        id_account: [],
        transaction_type: [],
        pagination: null
      }
    });
    this.setState({ search: null });
    getLocationTypes();
  };

  // Handle action di table
  handleRecord = (action, id) => {
    if (action === "activate") {
      this.props.activateLocationType(id);
    } else {
      this.props.deactivateLocationType(id);
    }
  };

  // Handle pagination
  handlePageChange = page => {
    const { getLocationTypes } = this.props;
    const { search } = this.props.locationType;
    const { filter } = this.props.locationType;
    getLocationTypes(page, search, filter);
  };

  listAction = {
    title: "",
    dataIndex: "id_location_type",
    rowKey: "id_location_type",
    width: "10%",
    align: "center",
    fixed: "right",
    render: (text, row) => (
      <div>
        <Tooltip
          placement="topLeft"
          title={<IntlMessages id="tooltip.update" />}
        >
          <Link to={`${this.props.match.path}/update/${text}`}>
            <Button size="small" className="invoiceEdtBtn">
              <i className="ion-android-create" />
            </Button>
          </Link>
        </Tooltip>

        {row.is_active ? (
          <Popconfirms
            title={<IntlMessages id="locationTypes.popconfirm.deactivate" />}
            onConfirm={this.handleRecord.bind(this, "deactivate", text)}
            okText={getLanguage({ id: "yes" })}
            cancelText={getLanguage({ id: "no" })}
          >
            <Tooltip
              placement="topRight"
              title={<IntlMessages id="tooltip.deactivate" />}
            >
              <Button size="small" className="invoiceDltBtn">
                <i className="ion-close-circled" />
              </Button>
            </Tooltip>
          </Popconfirms>
        ) : (
          <Popconfirms
            title={<IntlMessages id="locationTypes.popconfirm.activate" />}
            onConfirm={this.handleRecord.bind(this, "activate", text)}
            okText={getLanguage({ id: "yes" })}
            cancelText={getLanguage({ id: "no" })}
          >
            <Tooltip
              placement="topRight"
              title={<IntlMessages id="tooltip.activate" />}
            >
              <Button size="small" className="invoiceRstBtn">
                <i className="ion-refresh" />
              </Button>
            </Tooltip>
          </Popconfirms>
        )}
      </div>
    )
  };

  render() {
    const columns = [
      {
        title: getLanguage({ id: "code" }),
        dataIndex: "location_type_code",
        rowKey: "location_type_code",
        width: "25%",
        render: text => <span>{text}</span>
      },
      {
        title: getLanguage({ id: "name" }),
        dataIndex: "location_type_name",
        rowKey: "location_type_name",
        width: "25%",
        render: text => <span>{text}</span>
      },
      {
        title: getLanguage({ id: "label.description" }),
        dataIndex: "description",
        rowKey: "description",
        width: "30%",
        render: display => {
          if (display) {
            if (display.length > 50) {
              return (
                <Tooltip placement="bottom" title={display}>
                  {display.substring(0, 50) + "..."}
                </Tooltip>
              );
            } else {
              return display;
            }
          } else {
            return "";
          }
        }
      },
      {
        title: getLanguage({ id: "status" }),
        dataIndex: "is_active",
        rowKey: "is_active",
        width: "25%",
        align: "center",
        render: text => {
          let className;
          let display;
          if (text === 1) {
            className = "active";
            display = getLanguage({ id: "active" });
          } else {
            className = "nonactive";
            display = getLanguage({ id: "inactive" });
          }
          return <StatusTag className={className}>{display}</StatusTag>;
        }
      }
    ];
    // Data setup
    const { location_types } = this.props.locationType;
    //console.log(this.props.locationType); //console.log(this.state);
    const dataSource = [];
    const { user } = this.props.auth;
    let { permissions } = user ? user : [];
    permissions = permissions ? permissions : [];

    //console.log(permissions);
    if (permissions.includes("Update Location Type") && columns.length === 4) {
      columns.push(this.listAction);
    }

    Object.keys(location_types).map((locationType, index) => {
      return dataSource.push({
        ...location_types[locationType],
        key: locationType
      });
    });

    // Pagination Setup
    const { current_page, page_size, total, loading } = this.props.locationType;

    //console.log(this.props);
    return (
      <LayoutWrapper>
        <Box>
          <PageHeader
            title={<IntlMessages id="locationTypes.pageHeader" />}
            marginBottom={true}
          >
            <div>
              {permissions.includes("Create Location Type") ? (
                <Link to={`${this.props.match.path}/add`}>
                  <Button
                    // loading={loading}
                    // disabled={loading}
                    type="primary"
                    className="mateAddInvoiceBtn"
                  >
                    <IntlMessages id="locationTypes.button.add" />
                  </Button>
                </Link>
              ) : null}
              <Button
                onClick={() => this.props.history.replace(redirectTo)}
                style={{ marginLeft: "5px" }}
              >
                <IntlMessages id="button.back" />
              </Button>
            </div>
          </PageHeader>
          <div className="isoInvoiceTableBtn">
            <Search
              placeholder={
                getLanguage({ id: "code" }) + ", " + getLanguage({ id: "name" })
              }
              onSearch={this.handleSearch}
              style={{ marginRight: "1%" }}
              value={this.state.search}
              onChange={this.handleSearchValue}
            />
            <div
              style={{
                // width: "20%",
                marginTop: "auto",
                marginRight: "1%"
              }}
            >
              <Button
                type="primary"
                onClick={this.handleSearch.bind(this, this.state.search)}
              >
                <IntlMessages id="button.search" />
              </Button>
            </div>

            <div
              style={{
                width: "30%",
                marginTop: "auto",
                marginRight: "27%"
                // marginLeft: "-2%"
              }}
            >
              <Button onClick={this.handleResetSearch}>
                <IntlMessages id="filter.reset" />
              </Button>
            </div>
          </div>

          <CardWrapper>
            {location_types.length === 0 && !this.props.locationType.loading ? (
              <Empty
                description={<IntlMessages id="locationTypes.table.noData" />}
              />
            ) : (
              <div className="isoInvoiceTable">
                <Scrollbars style={{ width: "100%" }}>
                  <TableWrapper
                    dataSource={dataSource}
                    columns={columns}
                    loading={this.props.locationType.loading}
                    className="invoiceListTable"
                    bordered={true}
                    // Set pagination, jika ada page_size mengunakan pagination API
                    // jika tidak ada page_size menggunakan pagination bawaan
                    pagination={
                      page_size
                        ? {
                            current: current_page,
                            pageSize: parseInt(page_size),
                            total: total,
                            onChange: this.handlePageChange.bind(this)
                          }
                        : true
                    }
                  />
                </Scrollbars>
              </div>
            )}
          </CardWrapper>
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    locationType: state.locationType,
    auth: state.auth
  };
};

export default compose(connect(mapStateToProps, actions))(LocationType);
