const actions = {
    getLocationTypes: (page, search, filter) => ({
      type: 'GET_LOCATION_TYPES_REQUEST',
      payload: {
        page: page,
        search: search,
        filter: filter
      }
    }),
    createLocationType: () => ({
      type: 'CREATE_LOCATION_TYPE_REQUEST'
    }),
    storeLocationType: data => ({
      type: 'STORE_LOCATION_TYPE_REQUEST',
      payload: { body: data }
    }),
    editLocationType: id_location_type => ({
      type: 'EDIT_LOCATION_TYPE_REQUEST',
      payload: { id_location_type: id_location_type }
    }),
    updateLocationType: (id_location_type, data) => ({
      type: 'UPDATE_LOCATION_TYPE_REQUEST',
      payload: {
        id_location_type: id_location_type,
        body: data
      }
    }),
    showLocationType: id_location_type => ({
      type: 'SHOW_LOCATION_TYPE_REQUEST',
      payload: { id_location_type: id_location_type }
    }),
    deactivateLocationType: id_location_type => ({
      type: 'DEACTIVATE_LOCATION_TYPE_REQUEST',
      payload: { id_location_type }
    }),
    activateLocationType: id_location_type => ({
      type: 'ACTIVATE_LOCATION_TYPE_REQUEST',
      payload: { id_location_type }
    }),
    clearSuccess: () => ({
      type: "CLEAR_SUCCESS"
    }),
    clearError: () => ({
      type: "CLEAR_ERROR"
    }),
    clearErrorField: () => ({
      type: "CLEAR_ERROR_FIELD"
    })
  };
  
  export default actions;
  